import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';

import { TeamService } from '../../angular-services/team.service';
import { Team } from '../../classes/team';

@Component({
  selector: 'app-edit-team-form-admin',
  templateUrl: './edit-team-form.component.html',
  styleUrls: ['./edit-team-form.component.css']
})
export class EditTeamFormComponent implements OnInit {

  @Output() teamName = new EventEmitter<string>();
  formEditTeam: FormGroup;
  alert: any = { type: '', message: '', show: false };
  team: Team = new Team();
  mediaType = 'teams';
  urlObjects: string = 'https://viruscursinho.com.br:3002/teams';
  showModalSelectFile: boolean = false;

  constructor(private teamService: TeamService, private route: ActivatedRoute) { }

  ngOnInit() {
    this.initForm();
    this.getTeam();
  }

  initForm() {
    this.formEditTeam = new FormGroup({
      name: new FormControl('', Validators.required),
      description: new FormControl('', Validators.required),
      photo: new FormControl(''),
      status: new FormControl('', Validators.required),
    });
  }

  getTeam() {
    let key = this.route.snapshot.paramMap.get('id');
    this.teamService.getTeam(key).subscribe(
      response => {
        if (response !== undefined) {
          this.team = response.team;
          this.teamName.emit(this.team.name);
          this.loadForm();
        } else {
          this.alert.type = 'danger';
          this.alert.message = 'Houve algum erro ao salvar os dados!';
          this.alert.show = true;
        }
      }
    )
  }

  loadForm() {
    this.formEditTeam.patchValue({
      name: this.team.name,
      description: this.team.description,
      status: this.team.status,
      photo: this.team.photo
    });
  }

  save() {
    if (this.formEditTeam.valid) {
      let key = this.route.snapshot.paramMap.get('id');
      let data = this.formEditTeam.value;
      if (data.status === "true") {
        data.status = true;
      } else {
        data.status = false;
      }
      this.teamService.updateTeam(key, data).subscribe(
        response => {
          if (response !== undefined) {
            this.alert.type = 'success';
            this.alert.message = 'Dados salvos com sucesso!';
            this.alert.show = true;
          } else {
            this.alert.type = 'danger';
            this.alert.message = 'Houve algum erro ao salvar os dados!';
            this.alert.show = true;
          }
        }
      )
    }
  }

  changeStatusModalSelectFile() {
    if (this.showModalSelectFile) {
      this.showModalSelectFile = false;
    } else {
      this.showModalSelectFile = true;
    }
  }

  setUrlObject(link) {
    this.formEditTeam.patchValue({
      photo: link
    });
    this.showModalSelectFile = false;
  }

}
